import axios from "apis";

/**
 *     ,    API
 * @param {number} lat
 * @param {number} lon
 * @returns {Promise<{ temperature: string, condition: string }>}
 */
export const getWeatherData = async (lat: number, lon: number) => {
  const apiUrl = `https://api.openweathermap.org/data/2.5/weather?lat=${lat}&lon=${lon}&appid=${process.env.REACT_APP_OPENWEATHER_KEY}`;
  try {
    const res = await axios.get(apiUrl);

    const tempKelvin = res.data.main.temp; //
    const tempCelsius: string | null = (tempKelvin - 273.15).toFixed(); //
    const condition: string | null = res.data.weather[0].main; //,,  

    return { temperature: tempCelsius, condition };
  } catch (err) {
    console.error(err);
    throw err;
  }
};
